/**
 * Trigger an immediate page refresh when an update is found
 * The forceReload flag is deprecated, but may still needed to fully invalidate
 * a stale service worker
 * @see https://github.com/gatsbyjs/gatsby/issues/9087
 * Also
 * @see https://github.com/dimitridumont/clear-cache and 
 * https://github.com/dimitridumont/clear-cache/issues/1
 * (To investigate further)
 */

export const onServiceWorkerUpdateReady = () => {
  console.debug(`About to force reload.`);
  window.location.reload(true);
};
